.SpellWord {
    margin: auto;
    max-width: 640px;
}

.result {
    font-size: 1.2rem;

    &.-correct {
        color: var(--color-green);
    }

    &.-wrong {
        color: var(--color-red);
    }
}

.img-holder {
    width: 300px;
    height: 200px;
    margin: 0 auto var(--spacing-half);
}

.img-holder > img {
    object-fit: contain;
    object-position: center;
    display: block;
    width: 100%;
    height: 100%
}

.word-block {
    border-radius: var(--standard-border-radius);
    border: 1px solid var(--border-color);
    margin: auto;
    max-width: 640px;
    padding: 2em;
    text-align: center;
}

.transcription {
    font-family: monospace;
    letter-spacing: 3px;
}